/*========= ローディング画面のためのCSS ===============*/
#splash {
  position: fixed;
  width: 100%;
  height: 100%;
  background: #000;
  z-index: 9999999;
  text-align: center;
  color: #fff;
}

#splash-logo {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.splashbg {
  display: none;
}

body.appear .splashbg {
  display: block;
  content: "";
  position: fixed;
  z-index: 9999;
  width: 100%;
  height: 100vh;
  top: 0;
  left: 0;
  transform: scaleX(0);
  background-color: #960021; /*伸びる背景色の設定*/
  animation-name: PageAnime;
  animation-duration: 0.8s;
  animation-timing-function: ease-in-out;
  animation-fill-mode: forwards;
}

/*画面遷移の後現れるコンテンツ設定*/
#l-container {
  opacity: 0; /*はじめは透過0に*/
}

/*bodyにappearクラスがついたら出現*/
body.appear #l-container {
  animation-name: PageAnimeAppear;
  animation-duration: 1s;
  animation-delay: 0.8s;
  animation-fill-mode: forwards;
  opacity: 0;
}

/* loading ※https://projects.lukehaas.me/css-loaders/参照*/
.loader,
.loader:before,
.loader:after {
  background: #ffffff;
  animation: load1 1s infinite ease-in-out;
  width: 0.5em;
  height: 1em;
}

.loader {
  color: #ffffff;
  text-indent: -9999em;
  margin: 88px auto;
  position: relative;
  font-size: 11px;
  transform: translateZ(0);
  animation-delay: -0.16s;
}

.loader:before,
.loader:after {
  position: absolute;
  top: 0;
  content: "";
}

.loader:before {
  left: -1.5em;
  animation-delay: -0.32s;
}

.loader:after {
  left: 1.5em;
}

.p-visual {
  background: #ffffff;
  height: 100svh;
  position: relative;
  font-family: "Oswald", sans-serif;
  overflow: hidden;
}
.p-visual:before {
  content: "";
  background-color: #d11616;
  width: 105%;
  height: 150%;
  position: fixed;
  left: -54.5%;
  top: -47%;
  transform: rotate(10deg);
  clip-path: inset(0 0 100% 0);
  transition: clip-path 0.4s ease;
}
.p-visual:after {
  content: "";
  background-color: #111;
  width: 105%;
  height: 150%;
  position: fixed;
  left: -54.9%;
  top: -47%;
  transform: rotate(10deg);
  clip-path: inset(0 0 100% 0);
  clip-path: inset(0);
  transition: clip-path 1s cubic-bezier(0.165, 0.84, 0.44, 1) 0.8s;
}
.p-visual__inner {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  z-index: 1;
  position: fixed;
  width: 100%;
}
@media screen and (max-width: 767px) {
  .p-visual__inner {
    flex-direction: column-reverse;
  }
}
.p-visual__lead {
