  opacity: 0;
}
@media screen and (max-width: 767px) {
  .p-visual__img {
    width: 100vw;
    margin: 0;
  }
}
.p-visual__ttl {
  line-height: 1.3;
  margin-bottom: 2.2vw;
}
.p-visual__ttlAcc {
  display: block;
  letter-spacing: 0.8em;
  font-size: 0.5em;
}

body.appear .p-visual:before {
  clip-path: inset(0);
  transition-delay: 2.4s;
}
body.appear .p-visual__img {
  animation: fadeUpAnime ease 1s forwards 1.6s;
}

.scrolldown1 {
  position: fixed;
  left: 40px;
  bottom: 3%;
  height: 50px;
  z-index: 2;
}

.scrolldown1 span {
  position: absolute;
  color: #eee;
  font-size: 0.7rem;
  letter-spacing: 0.1em;
  transform: rotate(-90deg);
  text-transform: uppercase;
  left: -20px;
  top: -32px;
}

.scrolldown1::after {
  content: "";
  position: absolute;
  top: 0;
  width: 1px;
  height: 30px;
  background: #fff;
  animation: pathmove 1.4s ease-in-out infinite;
  opacity: 0;
}

