/* モーダルのスタイリング */
.modal {
  display: none; /* 初期状態では非表示 */
  position: fixed;
  z-index: 9999;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: rgba(0, 0, 0, 0.8);
}

.modal-content {
  background-color: #f8f9fa;
  margin: auto;
  padding: 20px;
  border: 1px solid #ced4da;
  border-radius: 5px;
  width: clamp(18.75rem, 5.114rem + 68.18vw, 56.25rem);
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
@media screen and (max-width: 767px) {
  .modal-content {
    height: 90svh;
    overflow-y: scroll;
  }
}

/* 閉じるボタンのスタイリング */
.close {
  color: #6c757d;
  position: absolute;
  top: 0px;
  right: 10px;
  font-size: 28px;
  font-weight: bold;
  cursor: pointer;
}
@media screen and (max-width: 767px) {
  .close {
    top: -5px;
  }
}

.close:hover,
.close:focus {
  color: #343a40;
  text-decoration: none;
  cursor: pointer;
}

/* リンクのスタイル */
.modalBtn {
  color: #222222;
  text-decoration: none;
  cursor: pointer;
  display: block;
}

a.modalBtn:hover {
  color: #222222;
}

/* JSONデータがない場合の要素の非表示 */
.hide {
  display: none;
}

/* カードのスタイリング */
